import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Login from './views/Login.vue';
import Signup from './views/Signup.vue';
import UsersProfile from './views/UsersProfile.vue';
import store from './vuex';
// import store from './store.js'

Vue.use(Router)

const ifNotAuthenticated = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    next()
    return
  }
  next('/')
}
//const login=  () => import(/* webpackChunkName: "about" */ './views/Login.vue')


const router = new Router({
  mode: 'history',
  base: `${process.env.BASE_URL}theme/10/ar`,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      beforeEnter: ifNotAuthenticated,
      component: Login
    },
    {
      path: '/signup',
      name: 'signup',
      beforeEnter: ifNotAuthenticated,
      component: Signup
    },
    {
      path: '/users/:handle',
      name: 'user',
      component: UsersProfile
    },
    {
      path: '/users/:handle/scream/:screamId',
      name: 'notification',
      component: () => import(/* webpackChunkName: "about" */ './views/UsersProfile.vue')
    },
    {
      path: '/notifications',
      name: 'notifications',
      meta: {
        requiresAuth: true
      },
      component: () => import(/* webpackChunkName: "about" */ './views/Notifications.vue')
    }
  ]
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(route => route.meta.requiresAuth)) {
    if(store.getters.isAuthenticated) {
      next();
      return
    }
    next({name: 'home'})
  } else {
    next();
  }
})

export default router;
