<template>
  <v-app id="app" :style='myDynamicStyleVars'>


    <Navbar @open-menu="onSideMenuClick" @langChange="onLangChange"></Navbar>

    <v-navigation-drawer v-model="drawer" absolute temporary >
        <v-list-item>
            <v-list-item-avatar>
            <v-img :src="theme.organization.logo_icon" ></v-img>
            </v-list-item-avatar>

            <v-list-item-content>
            <v-list-item-title>{{theme.organization.name}}</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list dense>

            <v-list-item
            v-for="item in navMenuItems"
            :key="item.title"
            :to="item.href"
            link
            >
            <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
            </v-list-item>
        </v-list>
    </v-navigation-drawer>

    <v-content>
      <router-view></router-view>
    </v-content>

    <v-footer
      dark
      padless
    >
      <v-card
        flat
        tile
        class=" lighten-1 white--text text-center"
        :color="theme.colors.brandSecColor"
        width='100%'
      >

        <v-card-text class='footer-menu pt-0 pb-0' >
            <v-btn
            v-for="link in theme.footer.links"
            :key="link.title"
            :color="theme.colors.brandHTextColor"
            :href="link.href"
            text
            rounded
            class="my-2"
          >
            {{ link.title }}
          </v-btn>
        </v-card-text>

        <v-card-text>
          <v-btn
            v-for="icon in theme.footer.social_media"
            :key="icon.title"
            :href="icon.href"
            class="mx-4 white--text"
            icon
          >
            <v-icon size="24px">fab fa-{{ icon.title }}</v-icon>
          </v-btn>
        </v-card-text>

        <v-card-text class="white--text pt-0 ">
          {{theme.organization.about}}
        </v-card-text>

        <v-card-text class="white--text pt-0">
          {{ new Date().getFullYear() }}  {{$t('public.brandRights')}} <strong>{{theme.organization.name}}</strong> 
        </v-card-text>
      </v-card>
    </v-footer>


  </v-app>
</template>

<script>

//Vuex
import { mapGetters } from 'vuex';


// COMPONENTS
import router from './router'

import i18n from '@/plugins/i18n';
import Navbar from './components/Layout/AppNavbar';
import { log } from 'util';





export default {
  name: 'App',
  components: {
    Navbar
  },
  data() {    
    return { 
      drawer: null,
      navMenuItems: Object.values(i18n.messages[i18n.locale].navMenu),
      routeName:"",
        
    };
  },
  
  mounted() {
    var pathArray = this.$route.path.split('/');
    this.$store.dispatch('GET_ORG_THEME',{currentThemeVr: this.theme.theme_version, orgId: pathArray[2], currentLocale: this.locale})
    

  },

  methods: { 
   
    onSideMenuClick() {
      this.drawer = !this.drawer;
      
    },

    onLangChange() {
      this.navMenuItems = Object.values(i18n.messages[i18n.locale].navMenu);
    },

    
    
  },
 
  computed: {
    ...mapGetters(["theme", "locale"]),

    myDynamicStyleVars(){
        return {
            '--brandPrimColor': this.theme.colors.brandPrimColor,
            '--brandSecColor': this.theme.colors.brandSecColor,
            '--brandHTextColor': this.theme.colors.brandHTextColor,
            '--brandPTextColor': this.theme.colors.brandPTextColor,
            '--brandBlackColor': this.theme.colors.brandBlackColor,
            '--brandGrayColor': this.theme.colors.brandGrayColor,
            '--arfont': this.theme.colors.arfont,
            '--enfont': this.theme.colors.enfont,
           
        }
    }

  }
  

};
</script>

<style lang="scss">
    @import './src/assets/main.scss';

    .v-card__text.footer-menu {
      background: var(--brandGrayColor);
    }
</style>


