<template>
    <form @submit.prevent="loginHandlerSubmit" ref="form">
        <v-text-field
            v-model="loginUser.identity"
            :placeholder="$t('login.emailPlaceholder')"
            
            :loading="loadingForm"
            :color="theme.colors.brandPTextColor"
            :background-color="theme.colors.brandSecColor"
            autofocus
            light
            height="40"
            full-width
            append-icon="mdi-email-outline"
        ></v-text-field>
        <v-text-field
            v-model="loginUser.password"
            :append-icon="showPassword ? svg.visibility : svg.visibilityOff"
            :type="showPassword ? 'text' : 'password'"
            name="input-10-1"
            :placeholder="$t('login.passPlaceholder')"
            hint="At least 6 characters"
            :loading="loadingForm"
            @click:append="showPassword = !showPassword"
            :color="theme.colors.brandPTextColor"
            :background-color="theme.colors.brandSecColor"
            height="40"
            full-width
            autocomplete
        ></v-text-field>

        <!-------------------------  FORM ERRORS  ------------------->
        <div v-if="errors" class="subtitle1 text-center text-capitalize red--text">
            <p v-for="(errorMessage, i) in errors" :key="i">{{errorMessage}}</p>
            <!-- <span>Don't have an account? <router-link class="cyan--text" to="/signup">Sign Up Here</router-link></span> -->
        </div>
        <!-------------------------  END FORM ERRORS ------------------->

        <div class="mt-5">
            <v-btn type="submit" large :loading="loadingForm" class="mr-4" :color="theme.colors.brandPrimColor" elevation="0" dark>
                {{$t('login.signinBtn')}}
            </v-btn>
            <router-link class="v-btn v-btn--depressed theme--light v-size--large mr-4" to="/signup">{{$t('login.signupBtn')}}</router-link>            
        </div>
    </form>
</template>

<script>
// MIXINS
import { reset } from '@/mixins/mixins';
import i18n from '@/plugins/i18n';

// SVG ICONS
import { mdiEyeOutline, mdiEyeOffOutline  } from '@mdi/js';

//VUEX
import { mapGetters } from 'vuex';
import axios from 'axios';


export default {
    mixins: [reset],
    data: () => ({
        showPassword: false,
        loginUser: {
            identity: '',
            password: ''
        },
        emailRules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
        ],
        svg: {
            visibility: mdiEyeOutline,
            visibilityOff: mdiEyeOffOutline,
        },
    }),
    methods: {
        loginHandlerSubmit() {
            this.$store.dispatch('SIGN_IN', {
                identity: this.loginUser.identity,
                password: this.loginUser.password
            })
            .then(() => {
                this.$router.push({name: 'home'});
            })
        },
    },
    computed: {
        ...mapGetters(['errors', 'loadingForm', 'theme'])
    },
    beforeDestroy() {
        this.$store.dispatch('CLEAR_ERROR')
    },
    mounted() {

        

    },
}
</script>
<style lang="scss" scoped>

    fieldset {
        display: none;
    }
</style>
