<template>

    <v-app-bar
        color="white"
        
        >
            <router-link to="/"><v-img :src="theme.organization.logo" max-width="100" max-height="100" ></v-img>
            </router-link>            

            <div class="flex-grow-1"></div>


            <!-- <AppAuthMenu class="hidden-tab-and-down"></AppAuthMenu> -->
            <div  class="hidden-tab-and-down">
                <router-link class="main-nav-link mr-4 ml-4" v-for='item in navMenuItems' :key="item.title" :to="item.href">{{item.title}}</router-link> 
                <router-link class="main-nav-link mr-4 ml-4" v-if="isAuthenticated" :to="$t('navMenu.profile.href')">{{$t('navMenu.profile.title')}}</router-link>            
            </div>
            <AppLogoutModal v-if="isAuthenticated"></AppLogoutModal>

            <div 
                 v-for="entry in languages"
                :key="entry.title"
                @click="changeLocale(entry.language)"
                class="lang-convert mr-4"
            >
                <flag 
                :iso="entry.flag" 
                v-bind:squared=false 
                v-if='entry.language !== locale' 
                />
            </div>

            <v-btn  icon @click="$emit('open-menu', 'true')"  class=" hidden-tab-and-top" fab>
                <v-icon>mdi-menu</v-icon>
            </v-btn>

            

        </v-app-bar>
    
    
</template>

<style lang="scss" scoped>
    header.v-toolbar {
        flex: 0 !important;

    }

    .lang-convert {
        cursor: pointer;
    }

    a.main-nav-link{
    color: var(--brandHTextColor) !important;
    font-size: 1rem;
    &:hover{
      color: var(--brandPrimColor) !important;
    }
  }

    @media  only screen and (max-width: 993px) {
        .hidden-tab-and-down {
            display: none !important;
        }
    }

    @media  only screen and (min-width: 994px) {
        .hidden-tab-and-top {
            display: none !important;
        }
    }
</style>


<script>

import AppLogoutModal from '@/components/AppLogoutModal.vue';



//VUEX
import { mapGetters } from 'vuex';
import i18n from '@/plugins/i18n';

import { mdiLogin, mdiLogout  } from '@mdi/js';




export default {
    components: {
        AppLogoutModal,
    },
    
    data: () => ({
        navMenuItems: Object.values(i18n.messages[i18n.locale].navMenu),
        languages: [            
            { flag: 'us', language: 'en', title: i18n.messages[i18n.locale].public.en },            
            { flag: 'sa', language: 'ar', title: i18n.messages[i18n.locale].public.ar }       
        ],
        

        svg: {
            login: mdiLogin,
            logout: mdiLogout,
        }, 
       
    }),
    computed: {
        ...mapGetters(['isAuthenticated', 'userNotifications', 'locale', 'theme'])
    },
    methods: {
        changeLocale(locale) {

            i18n.locale = locale;
            this.$emit("langChange", locale);
            this.navMenuItems = Object.values(i18n.messages[i18n.locale].navMenu);
            this.$store.dispatch('UPDATE_LOCALE', locale);


            if (locale === 'ar' ) {
                this.$vuetify.rtl = true;
            } else {
                this.$vuetify.rtl = false;
            }
            
        }
    },
    mounted() {
        this.locale == 'ar' ? this.$vuetify.rtl = true : this.$vuetify.rtl = false;
    
    }
}
</script>

